html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
  background: #f89e3e;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.card__txt {
  text-align: center;
  margin: 0;
}

.bg-amarillo {
  background-color: #c72939;
  padding: 4rem 2rem;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .bg-amarillo form {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 3rem;
  }
}
.bg-amarillo form legend {
  display: flex;
  flex-direction: column;
  place-items: center;
  row-gap: 2rem;
  color: #FFFFFF;
  font-size: 3rem;
  text-align: center;
}
.bg-amarillo form legend span {
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 600;
  text-align: center;
}
.bg-amarillo form .campos {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .bg-amarillo form .campos {
    align-items: center;
    flex-direction: row;
    column-gap: 2rem;
  }
}
.bg-amarillo form .campos .campo {
  display: flex;
  align-items: center;
  flex: auto;
}
.bg-amarillo form .campos .campo label {
  font-size: 2.5rem;
}
.bg-amarillo form .campos .campo input {
  outline: none;
  border: none;
  border-radius: 1rem;
  height: 2.8rem;
  width: calc(100% - 2rem);
}
@media (min-width: 768px) {
  .bg-amarillo form .campos .campo input {
    width: auto;
  }
}
.bg-amarillo form .btn {
  background-color: #f89e3e;
  color: #FFFFFF;
  font-size: 1.5rem;
}

td i {
  font-size: 2rem;
}

.error {
  background-color: #b90000;
}

.correcto {
  background-color: #00b900;
}

.notificacion {
  position: fixed;
  top: 1rem;
  right: 1rem;
  padding: 1rem 3rem;
  opacity: 0;
  transition: opacity 2s ease-in-out;
}

.PopUp {
  opacity: 1;
  transition: opacity 1s ease;
}

.contenedor-barra h1 {
  font-weight: 600;
  text-transform: uppercase;
}

.contenedor-tabla {
  width: 100%;
  overflow-x: scroll;
}

.contactos {
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 4rem 2rem;
  margin-bottom: 2rem;
}
.contactos h1 {
  font-weight: 600;
  text-transform: uppercase;
}
.contactos .contenedor-contactos {
  height: 100%;
  width: 90%;
  margin: 0 auto;
}
.contactos input {
  width: 100%;
  height: 4rem;
  padding: 1rem;
  border: 1px solid #0e1041;
  border-radius: 1rem;
}
.contactos table {
  width: 100%;
  margin-top: 1rem;
  border-collapse: collapse;
  overflow: scroll;
}
.contactos table thead {
  background-color: #c72939;
  color: #FFFFFF;
  text-transform: uppercase;
}
.contactos table thead th {
  padding: 1rem 0;
}
.contactos table tbody td {
  padding: 0.5rem;
}

.total-contactos {
  margin: 2rem 0;
  text-align: center;
  color: #f89e3e;
  font-size: 2rem;
  display: flex;
  column-gap: 2rem;
  justify-content: center;
  align-items: center;
}
.total-contactos span {
  font-size: 4rem;
}